import React, { useState } from 'react'
import { Badge, Button, Col, Container, Form, Row, Table } from 'react-bootstrap'
import Modal from 'react-bootstrap/Modal'

const Index = () => {
  const [filter, setFilter] = useState('Admin')
  const [show, setShow] = useState(false)
  const [name, setname] = useState('')
  const [mobileNumber, setMobileNumber] = useState('')
  const [role, setRole] = useState('')
  const [email, setEmail] = useState('')
  const [createdBy, setCreatedBy] = useState('')
  const [panNo, setPanNo] = useState('')
  const [uniqueId, setUniqueId] = useState('')
  const [aadharCard, setAadharCard] = useState('')
  const [partnerCreated, setPartnerCreated] = useState('')
  const [address, setAddress] = useState('')
  const [city, setCity] = useState('')
  const [pinCode, setPinCode] = useState('')

  const handleClose = () => setShow(false)
  const handleShow = () => setShow(true)
  return (
    <Container fluid>
      <div className="d-flex justify-content-between">
        <h3>Master Admin All Users</h3>
        <Form>
          <Row className="mb-3">
            <Form.Group className="mb-3" controlId="formBasicEmail">
              <Form.Select onChange={(e) => setFilter(e.target.value)}>
                <option disabled>Filter By</option>
                <option value={'Admin'}>Admin</option>
                <option value={'MasterDistributor'}>Master Distributor</option>
                <option value={'Distributor'}>Distributor</option>
                <option value={'Retailer'}>Retailer</option>
              </Form.Select>
            </Form.Group>
          </Row>
        </Form>
      </div>
      <Table responsive>
        <thead>
          <tr>
            <th className="text-center">Sn</th>
            <th className="text-center">Name</th>
            <th className="text-center">Role</th>
            <th className="text-center">Unique Id</th>
            <th className="text-center">PAN Card</th>
            <th className="text-center">Status</th>
            <th className="text-center">Actions</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td className="text-center">1</td>
            <td className="text-center">Username</td>
            <td className="text-center">Role</td>
            <td className="text-center">ADI-67921</td>
            <td className="text-center">ABSAKNJ</td>
            <td className="text-center">
              <Badge className="bg-success">Approved</Badge>
            </td>
            <td className="text-center">
              <Button className="myCustomBtn" onClick={() => handleShow()}>
                View More
              </Button>
            </td>
          </tr>
        </tbody>

        <Modal size="lg" show={show} onHide={handleClose}>
          <Modal.Header closeButton>
            <Modal.Title>User Details</Modal.Title>
          </Modal.Header>
          <Container fluid className='p-5'>

            <Row className='mb-3'>
              <Form.Group
                as={Col}
                className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                controlId="formName"
              >
                <Form.Label>Name</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter Name"
                  onChange={(e) => {
                    setname(e.target.value)
                  }}
                  value={name}
                  required disabled
                />
              </Form.Group>
              <Form.Group
                as={Col}
                className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                controlId="formMobileNumber"
              >
                <Form.Label>WhatsApp Number</Form.Label>
                <Form.Control
                  type="number"
                  placeholder="Enter Mobile Number"
                  onChange={(e) => setMobileNumber(e.target.value)}
                  value={mobileNumber}
                  required disabled
                />
              </Form.Group>
              <Form.Group
                as={Col}
                className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                controlId="formMobileNumber"
              >
                <Form.Label>Role</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter Mobile Number"
                  onChange={(e) => setRole(e.target.value)}
                  value={role}
                  required disabled
                />
              </Form.Group>
            </Row>
             <Row className='mb-3'>
              <Form.Group
                as={Col}
                className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                controlId="formName"
              >
                <Form.Label>Email</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Email"
                  onChange={(e) => {
                    setEmail(e.target.value)
                  }}
                  value={email}
                  required disabled
                />
              </Form.Group>
              <Form.Group
                as={Col}
                className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                controlId="formMobileNumber"
              >
                <Form.Label>Created By</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Created By"
                  onChange={(e) => setCreatedBy(e.target.value)}
                  value={createdBy}
                  required disabled
                />
              </Form.Group>
              <Form.Group
                as={Col}
                className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                controlId="formMobileNumber"
              >
                <Form.Label>PAN No.</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="PAN No."
                  onChange={(e) => setPanNo(e.target.value)}
                  value={panNo}
                  required disabled
                />
              </Form.Group>
            </Row>
            <Row className='mb-3'>
              <Form.Group
                as={Col}
                className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                controlId="formName"
              >
                <Form.Label>Unique ID</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Unique ID"
                  onChange={(e) => {
                    setUniqueId(e.target.value)
                  }}
                  value={uniqueId}
                  required disabled
                />
              </Form.Group>
              <Form.Group
                as={Col}
                className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                controlId="formMobileNumber"
              >
                <Form.Label>Aadhar Card</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Aadhar Card"
                  onChange={(e) => setAadharCard(e.target.value)}
                  value={aadharCard}
                  required disabled
                />
              </Form.Group>
              <Form.Group
                as={Col}
                className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                controlId="formMobileNumber"
              >
                <Form.Label>Total Partner Created</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Total Partner Created"
                  onChange={(e) => setPartnerCreated(e.target.value)}
                  value={partnerCreated}
                  required disabled
                />
              </Form.Group>
            </Row>
            <Row className='mb-3'>
              <Form.Group
                as={Col}
                className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                controlId="formName"
              >
                <Form.Label>Address</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Address"
                  onChange={(e) => {
                    setAddress(e.target.value)
                  }}
                  value={address}
                  required disabled
                />
              </Form.Group>
              <Form.Group
                as={Col}
                className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                controlId="formMobileNumber"
              >
                <Form.Label>City</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="City"
                  onChange={(e) => setCity(e.target.value)}
                  value={city}
                  required disabled
                />
              </Form.Group>
              <Form.Group
                as={Col}
                className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                controlId="formMobileNumber"
              >
                <Form.Label>Pin Code</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Pin code"
                  onChange={(e) => setPinCode(e.target.value)}
                  value={pinCode}
                  required disabled
                />
              </Form.Group>
            </Row>
            
            </Container>
        </Modal>
      </Table>
    </Container>
  )
}

export default Index
